@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Noto Sans JP', sans-serif;
}


@media screen {
    header {
        height: 70px;
        background-color: coral;
        display: flex;
        align-self: center;
        justify-content: space-between;
        box-shadow: 1px 1px 1px 1px rgba(0,0,0,0.15);

    }

    .header-right {
        display: flex;
        text-decoration: none;
    }
}
@media screen {
    .home-content {
        padding-top: 30px;
        padding-left: 150px;
        padding-right: 150px;
        padding-bottom: 30px;
        text-align: center;
    }

    .skill-set {
        justify-content: space-between;
        display: flex;
    }

    .skill-lang-box {
        width: 48%
    }

    .skill-lang {
        justify-content: center;
        display: flex;
        flex-wrap: wrap;
        border: solid 1px #00A8CC;
        border-radius: 1%;
    }

    .skill-lang h4 {
        margin: 10px;
    }

    .skill-middle {
        justify-content: center;
        display: flex;
        flex-wrap: wrap;
        border: solid 1px #00A8CC;
        border-radius: 1%;
    }

    .skill-middle-box {
        width: 48%;
    }

    .skill-middle h4 {
        margin: 10px;
    }

    .skill-tools {
        justify-content: center;
        display: flex;
        flex-wrap: wrap;
        border: solid 1px #00A8CC;
        border-radius: 1%;
    }

    .skill-tools h4 {
        margin: 10px;
    }
}
@media screen {
    .Logo{
        height: 100%;
        display: flex;
        justify-content: center;
        align-self: center;

        transition: all 0.3s;
    }

    .Logo p{
        color: black;
        font-size: 25px;
        font-weight: bold;
        letter-spacing: 1px;
    }
}
@media screen {
    .work-contents {
        text-align: center;
        padding-top: 30px;
        padding-left: 150px;
        padding-right: 150px;
        padding-bottom: 30px;
    }
}
@media screen {
    .contact-content {
        text-align: center;
    }
    .sns-icons, .mail-addr {
        font-size: large;
    }
}
@media screen {
    .NavButton{
        height: 100%;
        width: 150px;
        margin-left: 15px;
        margin-right: 15px;

        display: flex;
        justify-content: center;
        align-self: center;

        transition: all 0.2s;
    }

    .NavButton:hover{
        background-color: rgb(240, 240, 240);
    }

    .NavButton p{
        margin-top: 30px;
        font-size: 18px;
        color: black;
        font-weight: bolder;

        transition: all 0.3s;
    }
}
